import React, { useState, useEffect } from 'react';
import Select from 'react-select';
import { MainButton, WebAppProvider, useWebApp } from '@vkruglikov/react-telegram-web-app';
import { DayPicker } from 'react-day-picker';
import 'react-day-picker/dist/style.css';
import './App.css'

const CountryCitySelect = () => {
    const [countries, setCountries] = useState([]);
    const [cities, setCities] = useState([]);
    const [selectedCountry, setSelectedCountry] = useState('');
    const [selectedCity, setSelectedCity] = useState('');
    const [startDate, setStartDate] = useState(null);
    const [endDate, setEndDate] = useState(null);
    const webApp = useWebApp();

    useEffect(() => {
        fetch('countries.json')
            .then(response => response.json())
            .then(data => setCountries(data));
    }, []);

    const handleCountryChange = (selectedOption) => {
        setSelectedCountry(selectedOption);
        setSelectedCity(null);
        if (selectedOption) {
            const country = selectedOption.value
            fetch(`countries/${country}.json`)
                .then(response => response.json())
                .then(data => setCities(data));
        } else {
            setCities([]);
        }
    };

    const handleCityChange = (selectedOption) => {
        setSelectedCity(selectedOption);
    };

    const handleMainButton = () => {
        let lat = '';
        let long = '';
        for (let i = 0; i < cities.length; i++) {
            if (cities[i].title === selectedCity['value']) {
                lat = cities[i].lat;
                long = cities[i].long;
                break;
            }
        }
        webApp?.sendData(lat + '|'+long +'|'+startDate+'|'+endDate);
    }

    const countryOptions = countries.map(country => ({ value: country, label: country }));
    const cityOptions = cities.map(city => ({ value: city.title, label: city.title }));
    
    return (
        <WebAppProvider>
        <MainButton className="main_button_tg" onClick={handleMainButton}
                text="" />
            <div className="select-container">
                <Select
                    options={countryOptions}
                    value={selectedCountry}
                    onChange={handleCountryChange}
                    placeholder=" "
                />

                <Select
                    options={cityOptions}
                    value={selectedCity}
                    onChange={handleCityChange}
                    placeholder=" "
                />
            </div>
            <div className="datepicker-container">
                <label>:</label>
                <DayPicker
                    mode="single"
                    selected={startDate}
                    onSelect={date => setStartDate(date)}
                />
                <label>:</label>
                <DayPicker
                    mode="single"
                    selected={endDate}
                    onSelect={date => setEndDate(date)}
                />
            </div>
            
        </WebAppProvider>
    );
};

export default CountryCitySelect;

